<template>
  <div class="container">
    <div class="data">
      <div class="table">
        <edit-table
        :columns="columns"
        :data="data"
        rowKey="no"
        :currentEdit="currentEdit"
        @addRecord="addRecord"
        @edit="edit"
        @save="save"
        @removeRecord="removeRecord">
        </edit-table>
      </div>
    </div>
  </div>
</template>

<script>
import {generateUUID} from '@/utils/uuid'
const EditTable = () => import('@/components/EditTable')
export default {
  name: 'EditTest',
  components: {EditTable},
  data () {
    return {
      currentEdit: '',
      columns: [
        {prop: 'index', width: 50, label: '', type: 'selection'},
        {prop: 'name', width: '120', label: '姓名', type: 'input'},
        {prop: 'sex', width: '120', label: '性别', type: 'select', options: [{value: 'male', label: '男'}, {value: 'female', label: '女'}]},
        {prop: 'age', width: '120', label: '年龄', type: 'input'},
        {prop: 'birthday', width: '120', label: '生日', type: 'input'},
        {prop: 'test', width: '120', label: '未知'},
        {
          prop: 'operation',
          width: '120',
          label: '操作',
          operation: true,
          operations: [
            {text: '编辑', event: 'edit', icon: 'iconfont icon-bianji'},
            {text: '保存', event: 'save', icon: 'iconfont icon-fuzhi'},
            {text: '删除', event: 'removeRecord', icon: 'iconfont icon-iconfont-shanchu'}
          ]
        }
      ],
      data: [
        {no: '1', name: '张翰', sex: '男', age: '21', birthday: '2018-02-01', isEdit: false, test: '12'},
        {no: '2', name: '李明', sex: '男', age: '25', birthday: '2018-01-01', isEdit: false, test: '23'},
        {no: '3', name: '王珊', sex: '女', age: '32', birthday: '2018-02-02', isEdit: false, test: '45'}
      ]
    }
  },
  methods: {
    addRecord () {
      const no = generateUUID()
      const newItem = {no: no, isEdit: true, name: '', sex: '', age: '', birthday: '', test: 'test'}
      this.data.push(newItem)
      this.edit(no)
    },
    removeRecord (rowkey) {
      this.$confirm('确定删除此条数据记录吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const index = this.data.findIndex(item => item[this.rowKey] === rowkey)
        if (index === this.currentEditIndex) {
          this.currentEdit = ''
        }
        this.data.splice(index, 1)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      }).catch(() => {
      })
    },
    edit (rowkey) {
      if (this.currentEditIndex > -1) {
        this.data[this.currentEditIndex].isEdit = false
      }
      this.currentEdit = rowkey
      this.data[this.currentEditIndex].isEdit = true
    },
    save () {
      this.data[this.currentEditIndex].isEdit = false
      this.currentEdit = ''
    }
  },
  computed: {
    currentEditIndex () {
      return this.data.findIndex(item => item.no === this.currentEdit)
    }
  }
}
</script>

<style scoped>

</style>



